* {
	font-family: Helvetica, Arial, sans-serif;
}

body {
	padding: 60px;
}

input {
	border-radius: 6px;
	color: gray;
	font-size: 1.2rem;
	padding: 10px;
	width: 100%;
}

.card-grid {
	display: flex;
	flex-wrap: wrap;
}

.card {
	align-content: center;
	border: 1px solid grey;
	border-radius: 6px;
	height: 250px;
	margin-bottom: 10px;
	margin-right: 10px;
	overflow: hidden;
	width: 300px;
}

.card p {
	padding: 5px;
	text-align: center;
}

.card img {
	height: 180px;
	width: 100%;
}
